
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import PullTo from 'vue-pull-to'
import { checkLoginToLogin } from '@/api/login'
import * as base from '@/api/base'
import { agentQuery } from '@/api/keen'
Vue.use(LoadingPlugin)
export default {
components: {
PullTo,
XHeader,
Flexbox,
FlexboxItem,
XButton,
Checker,
CheckerItem,
Datetime,
Group
},
data () {
return {
dataList: [
'(ง •̀_•́)ง', '(´・ω・`) ', '（/TДT)/ ', '>ㅂ<',
'o(*≧▽≦)ツ', '(≖ ‿ ≖)✧', '(o^∇^o)ﾉ', ' (´・ω・)ﾉ',
'(´・ω・`)', 'ヽ(･ω･｡)ﾉ', '(｀･ω･´)', '╰(*°▽°*)╯',
'╮(￣▽￣)╭', '(￣▽￣)~*', '(⊙ˍ⊙)', '(￣0 ￣)y'
],
demo: 1,
value1: '',
iconLink: '',
curatorId: 0,
title: '棋牌室历史收入',
isAgent: base.getSingleUserInfo('user_identity').current === 'agent',
curatorInfo: {},
income: 0
}
},
methods: {
loadmore (loaded) {
setTimeout(() => {
this.dataList = this.dataList.concat(this.dataList)
loaded('done')
}, 2000)
},
stateChange (state) {
console.log(state)
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom'
} else if (state === 'loading') {
this.iconLink = '#icon-loading'
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish'
}
},
change (value) {
if (this.isAgent) {
this.value1 = value
agentQuery.queryCuratorOneDayIncome(this.$route.params.id, this.value1, data => {
this.income = data.result
this.$vux.toast.text('查询成功', 'middle')
})
}
}
},
created: function () {
checkLoginToLogin(this)
this.curatorId = this.$route.params.id
let todayDate = new Date()
this.value1 = todayDate.getFullYear() + '-' + (todayDate.getMonth() + 1) + '-' + todayDate.getDate()
if (this.isAgent) {
this.title = '下挂馆长详情'
let params = querystring.parse(location.href.split('?')[1])
this.curatorInfo = params
console.log(this.curatorInfo)
agentQuery.queryCuratorOneDayIncome(this.$route.params.id, this.value1, data => {
this.income = data.result
})
}
}
}



